.body-article {
    display: flex;
    align-items: center;
    justify-content: center;
}

.article {
    color: #fff;
    padding: 50px;
    margin: 0 auto;
    border: 10px solid #7943a4;
    border-radius: 10px;

    &__title {
        text-align: center;
        margin-bottom: 30px;
        font-size: 25px;
        font-weight: 800;
    }

    &__body {
        margin-bottom: 50px;
        font-size: 20px;
        text-align: center;
    }
}

.comments {
    &__item {
        padding-top: 20px;
        border-top: 1px solid #9d5cd0;

        max-width: fit-content;
        & + & {
            margin-top: 20px;
        }
    }

    &__name {
        margin-bottom: 10px;
        font-size: 20px;
    }

    &__text {
    }
}
