.blog-list {
    max-width: 1500px;
    padding: 20px;
    margin: 0 auto;
    height: 100%;

    &__item {
        min-height: 70px;
        position: relative;
        &:not(:last-child) {
            border-bottom: 1px solid #9d5cd0;
            &:hover {
                transition: border-color 0.3s;
                border-color: #7943a4;
            }
        }
    }

    &__link {
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        &:hover {
            transition: color 0.3s;
            color: #7943a4;
        }
    }
}

.nav {
    position: fixed;
    top: 20px;
    left: 0;
    &__list {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        max-height: 900px;
        column-gap: 10px;
    }

    &__link {
        display: block;
        color: #fff;
        min-height: 30px;
        width: 100%;
        border: 1px solid #9d5cd0;
        padding: 5px;
        text-align: center;
        margin-bottom: 20px;
        &:hover {
            transition: border-color, color 0.3s;
            border-color: #7943a4;
            color: #7943a4;
        }
    }
}
